___CSS_LOADER_EXPORT___.push([module.id, `/* Reset default browser styles for consistency */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Remove list styles */
ul,
ol {
  list-style: none;
}

/* Remove default hyperlink styles */
a {
  text-decoration: none;
  color: inherit;
}

/* Normalize button and input styles */
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  border: none;
  outline: none;
  background: none;
}

/* Remove default button cursor */
button {
  cursor: pointer;
}

/* Ensure images are responsive and don't exceed their container */
img {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Normalize typography */
body {
  line-height: 1.6;
  font-family: Arial, sans-serif;
}

/* Remove default focus outlines for accessibility-friendly styling */
:focus {
  outline: none;
}`, "",{"version":3,"sources":["webpack://./src/styles/reset.css"],"names":[],"mappings":"AAAA,iDAAiD;AACjD;;;EAGE,SAAS;EACT,UAAU;EACV,sBAAsB;AACxB;;AAEA,uBAAuB;AACvB;;EAEE,gBAAgB;AAClB;;AAEA,oCAAoC;AACpC;EACE,qBAAqB;EACrB,cAAc;AAChB;;AAEA,sCAAsC;AACtC;;;;EAIE,oBAAoB;EACpB,kBAAkB;EAClB,oBAAoB;EACpB,YAAY;EACZ,aAAa;EACb,gBAAgB;AAClB;;AAEA,iCAAiC;AACjC;EACE,eAAe;AACjB;;AAEA,kEAAkE;AAClE;EACE,eAAe;EACf,YAAY;EACZ,cAAc;AAChB;;AAEA,yBAAyB;AACzB;EACE,gBAAgB;EAChB,8BAA8B;AAChC;;AAEA,qEAAqE;AACrE;EACE,aAAa;AACf","sourcesContent":["/* Reset default browser styles for consistency */\n*,\n*::before,\n*::after {\n  margin: 0;\n  padding: 0;\n  box-sizing: border-box;\n}\n\n/* Remove list styles */\nul,\nol {\n  list-style: none;\n}\n\n/* Remove default hyperlink styles */\na {\n  text-decoration: none;\n  color: inherit;\n}\n\n/* Normalize button and input styles */\nbutton,\ninput,\nselect,\ntextarea {\n  font-family: inherit;\n  font-size: inherit;\n  line-height: inherit;\n  border: none;\n  outline: none;\n  background: none;\n}\n\n/* Remove default button cursor */\nbutton {\n  cursor: pointer;\n}\n\n/* Ensure images are responsive and don't exceed their container */\nimg {\n  max-width: 100%;\n  height: auto;\n  display: block;\n}\n\n/* Normalize typography */\nbody {\n  line-height: 1.6;\n  font-family: Arial, sans-serif;\n}\n\n/* Remove default focus outlines for accessibility-friendly styling */\n:focus {\n  outline: none;\n}"],"sourceRoot":""}]);
