:root {
  --default-text: #373530;
  --primary-color: #f5c542;
  --highlight-color: #f6a600;
  --grid-bg: #ccc;
  --cell-bg: #fff;
  --ship-color: #4caf50;
  --ship-border: #2e7d32;
  --preview-valid: #90ee90;
  --preview-invalid: #ff6347;
  --hit-color: #ff0000;
  --miss-color: #d3d3d3;
  --disabled-bg: #ccc;
}

body {
  background-color: #1a2b3c;
  color: var(--primary-color);
  font-family: Arial, sans-serif;
  text-align: center;
  margin: 0;
}

#game-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}

/* Common Screen Styles */
#loading-screen,
#ship-placement-screen,
#game-screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  color: var(--primary-color);
}

#loading-screen {
  padding-bottom: 128px;
  gap: 32px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.5) 53.67%), url(${___CSS_LOADER_URL_REPLACEMENT_0___}) lightgray 50% / cover no-repeat;
}

#ship-placement-screen {
  height: auto;
}

/* Typography */
h1 {
  color: var(--highlight-color);
  -webkit-text-stroke: 2px var(--default-text);
  font-family: "Inknut Antiqua", serif;
  font-size: 122px;
  font-weight: 400;
  line-height: normal;
}

h2 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  font-family: "Inknut Antiqua", serif;
  -webkit-text-stroke: 1px var(--default-text);
}

p {
  font-size: 1.2rem;
  margin-bottom: 1.5rem;
}

#ship-placement-screen p,
.board-container p {
  font-family: "Inknut Antiqua", serif;
}

#turn-message {
  font-size: 1.5rem;
}

/* Grid and Board Styles */
.game-container,
.boards-container {
  display: flex;
  gap: 2rem;
}

.boards-container {
  gap: 3rem;
}

.board-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.grid {
  display: grid;
  grid-template: repeat(10, 40px) / repeat(10, 40px);
  gap: 1px;
  background-color: var(--grid-bg);
}

.grid-cell {
  background-color: var(--cell-bg);
  border: 1px solid #999;
}

.grid-cell.occupied {
  background-color: var(--ship-color);
}

.grid-cell.preview.valid {
  background-color: var(--preview-valid);
}

.grid-cell.preview.invalid {
  background-color: var(--preview-invalid);
}

.grid-cell.hit {
  background-color: var(--hit-color);
}

.grid-cell.miss {
  background-color: var(--miss-color);
}

/* Ship Styles */
.ship-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.ship {
  display: inline-flex;
  flex-direction: row;
  gap: 1px;
  cursor: pointer;
  padding: 0;
  margin: 0;
  width: fit-content;
  height: fit-content;
}

.ship-segment {
  width: 40px;
  height: 40px;
  background-color: var(--ship-color);
  border: 1px solid var(--ship-border);
}

.ship.active {
  background-color: var(--preview-valid);
}

.ship.active .ship-segment {
  background-color: var(--preview-valid);
}

/* Buttons */
#start-game,
#continue-btn {
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background-color: transparent;
  border: 2px solid var(--highlight-color);
  color: var(--highlight-color);
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
  transition: all 0.3s ease;
}

#start-game {
  font-size: 14px;
}

#start-game:hover,
#continue-btn:hover {
  background-color: var(--highlight-color);
  color: #000;
}

#continue-btn:disabled {
  background-color: var(--disabled-bg);
  border-color: var(--disabled-bg);
  color: #1a2b3c;
  cursor: not-allowed;
}

/* Error Message */
#error-message {
  margin-top: 1rem;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA,mBAAmB;AACnB;EACE,uBAAuB;EACvB,wBAAwB;EACxB,0BAA0B;EAC1B,eAAe;EACf,eAAe;EACf,qBAAqB;EACrB,sBAAsB;EACtB,wBAAwB;EACxB,0BAA0B;EAC1B,oBAAoB;EACpB,qBAAqB;EACrB,mBAAmB;AACrB;;AAEA;EACE,yBAAyB;EACzB,2BAA2B;EAC3B,8BAA8B;EAC9B,kBAAkB;EAClB,SAAS;AACX;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,YAAY;EACZ,aAAa;AACf;;AAEA,yBAAyB;AACzB;;;EAGE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;EACnB,aAAa;EACb,2BAA2B;AAC7B;;AAEA;EACE,qBAAqB;EACrB,SAAS;EACT,4JAAiK;AACnK;;AAEA;EACE,YAAY;AACd;;AAEA,eAAe;AACf;EACE,6BAA6B;EAC7B,4CAA4C;EAC5C,oCAAoC;EACpC,gBAAgB;EAChB,gBAAgB;EAChB,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;EACjB,qBAAqB;EACrB,oCAAoC;EACpC,4CAA4C;AAC9C;;AAEA;EACE,iBAAiB;EACjB,qBAAqB;AACvB;;AAEA;;EAEE,oCAAoC;AACtC;;AAEA;EACE,iBAAiB;AACnB;;AAEA,0BAA0B;AAC1B;;EAEE,aAAa;EACb,SAAS;AACX;;AAEA;EACE,SAAS;AACX;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,aAAa;EACb,kDAAkD;EAClD,QAAQ;EACR,gCAAgC;AAClC;;AAEA;EACE,gCAAgC;EAChC,sBAAsB;AACxB;;AAEA;EACE,mCAAmC;AACrC;;AAEA;EACE,sCAAsC;AACxC;;AAEA;EACE,wCAAwC;AAC1C;;AAEA;EACE,kCAAkC;AACpC;;AAEA;EACE,mCAAmC;AACrC;;AAEA,gBAAgB;AAChB;EACE,aAAa;EACb,sBAAsB;EACtB,WAAW;AACb;;AAEA;EACE,oBAAoB;EACpB,mBAAmB;EACnB,QAAQ;EACR,eAAe;EACf,UAAU;EACV,SAAS;EACT,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,mCAAmC;EACnC,oCAAoC;AACtC;;AAEA;EACE,sCAAsC;AACxC;;AAEA;EACE,sCAAsC;AACxC;;AAEA,YAAY;AACZ;;EAEE,gBAAgB;EAChB,oBAAoB;EACpB,6BAA6B;EAC7B,wCAAwC;EACxC,6BAA6B;EAC7B,iBAAiB;EACjB,iBAAiB;EACjB,yBAAyB;EACzB,eAAe;EACf,yBAAyB;AAC3B;;AAEA;EACE,eAAe;AACjB;;AAEA;;EAEE,wCAAwC;EACxC,WAAW;AACb;;AAEA;EACE,oCAAoC;EACpC,gCAAgC;EAChC,cAAc;EACd,mBAAmB;AACrB;;AAEA,kBAAkB;AAClB;EACE,gBAAgB;AAClB","sourcesContent":["/* General Styles */\n:root {\n  --default-text: #373530;\n  --primary-color: #f5c542;\n  --highlight-color: #f6a600;\n  --grid-bg: #ccc;\n  --cell-bg: #fff;\n  --ship-color: #4caf50;\n  --ship-border: #2e7d32;\n  --preview-valid: #90ee90;\n  --preview-invalid: #ff6347